table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 2px 6px rgba(0,0,0,.25);
    border-radius: 8px;
    padding: 4px;
    box-sizing: border-box;

    & td,
    th {
        //border: 1px solid #dddddd;
        text-align: left;
        padding: 16px;
        border-bottom: 1px solid #ececec;
    }
    
    & th {
        cursor: pointer;
    }
}

button {
    background-color: #007aff;
    outline: none;
    border: none;
    color: #fff;
    height: 32px;
    margin: 0 8px;
    padding: 0 24px;
    border-radius: 8px;
    cursor: pointer;
}

.textbox {
    padding: 4px 8px;
    border-radius: 4px;
    outline: none;
    border: none;
    border: 1px solid #414141;
}

.header {
    width: 100%;
    display: flex;
    align-items: center;
    height: 96px;

    & h1 {
        margin-top: 0;
        flex: 1;
        margin: 0;
    }

    & .search-bar {
        height: 28px;
        display: flex;
        align-items: center;
        border-radius: 7px;
        border: 1px solid #d1d1d1;
        background: #fff;
        outline: none;
        font-family: "Google Sans";
        padding: 0 20px;
        font-size: 13px;
        transition: all .15s ease!important;
        font-weight: 500;

        & .icon {
            font-size: 16px;
            color: #233745;
            cursor: pointer;
        }
        
        & input {
            border: none;
            outline: none;
            background: transparent;
            height: 100%;
            font-size: 14px;
            margin-left: 8px;
        }
    }
}